import React, { Component } from 'react';

class Modal extends Component {
    render() {
        return <div className="modal fade bd-example-modal-lg" id={this.props.id} tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg" role="document">
              <div className="modal-content">
                <div className="modal-body px-0 py-2 p-md-0 pr-lg-2">
                <button type="button" className="close p-1" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                </button>
                  <div className="row mx-0">
                    <div className="col-lg-3 p-0 d-none d-lg-block">
                      <img src={ this.props.image } alt="" className="img-fluid" />
                    </div>
                    <div className="col-lg-6 py-md-3">
                    <h1 className="raleway-bold pb-3 mb-0">{this.props.name}'s Story</h1>
                      <div className="embed-responsive embed-responsive-4by3">
                        <iframe className="embed-responsive-item" src={ this.props.video} />
                      </div>
                    </div>
                    <div className="col-lg-3 pt-md-4 pb-3 text-center">
                      <h3 className="pt-lg-5 pt-0 raleway-bold text-dark text-left">
                        { this.props.heading }
                      </h3>
                      <hr className="" />
                      <p className="roboto-regular  text-left"> { this.props.desc } </p>
                      <a className="btn btn-success text-white text-uppercase btn-block font-sm-l" target="_blank" href={ this.props.blog }>Read {this.props.name}'s blog</a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
    }
}

export default Modal;


