import React, { Component } from 'react';
import Slider from "react-slick";
import data from './data.json';
import Modal from '../Common/modal';
import Slide from './slide';

class Card extends Component{
    render() {
        var settings = {
            arrows: true,
			centerMode: false,
			centerPadding: '40px',
			dots: false,
			infinite: true,
			slidesToShow: 4,
			slidesToScroll: 1,
			responsive: [{
				breakpoint: 769,
				settings: {
					dots: false,
					centerMode: true,
					slidesToShow: 3
				}
			}, {
				breakpoint: 577,
				settings: {
					dots: true,
					slidesToShow: 1
				}
			}]
        };

        return(
            <section id="goTroops">
                <h1 className="raleway-bold text-center h3 py-lg-5 py-md-3 pt-4 pb-3 text-dark" >
                    Learn more about the people who are driving impact at scale 
                </h1>
                <div className="">
                    <div className="row no-gutters">
                        {data.map((employee, key) =>
                            <div key={key} className="col test scroll" data-toggle="modal" data-target={"#" + employee.id}>
                                <img className="img-fluid fade-in" src={employee.image} alt={employee.name + "'s story"} />

                                <Modal
                                    id={employee.id}
                                    name={employee.name}
                                    heading={employee.heading}
                                    image={employee.image}
                                    video={employee.video}
                                    blog={employee.blog}
                                    desc={employee.desc}
                                />
                            </div>
                        )}
                    </div>
                </div>
                {/* <div className="row no-gutters d-block d-md-none"> */}
                    {/* <Slider {...settings} className="row home-slider d-md-none d-block text-center">
                        {
                            data.map((employee, key) =>
                            <Slide employee = { employee} key = { key } />)
                        }    
                    </Slider> */}
                    
                {/* </div> */}
            </section>
        );
    }
}

export default Card;


